import React, { Component } from "react";
import BLTMain from "../Home/images/BLT/1.png";
import bltimg2 from "../Home/images/buildwealth 1.png";
import { withTranslation } from "react-i18next";
import reactHtmlParser from 'react-html-parser';
import {Helmet} from 'react-helmet';

export class BLT extends Component {
  render() {
    const { t } = this.props;
    return (
      <main>
         <Helmet>
                <meta charSet="utf-8" />
                <link rel="canonical" href={window.location.href} />
            </Helmet>
        <div className="container container-wrapper">
          <div className="row">
            <div className="col-lg-7 col-md-6 mb-4">
              <h2>{t("BLT:BLT1")}</h2>
              <p>{t("BLT:BLT2")}</p>
              <div className="registerFair">
                <a
                  href="https://app.nivesh.com/home/index"
                  target="_blank"
                  title=""
                  className="btn btn-primary"
                >
                  {t('CTAFooter:InvestNow1')}
                </a>
              </div>
            </div>
            <div className="col-lg-5 col-md-6">
              <img src={BLTMain} className="img-fluid" />
            </div>
          </div>
        </div>

        <div className="landingPlatforms">
          <div className="container">
            <div className="row">
              <h2> {t("BLT:BLT3")}</h2>
              <p>{reactHtmlParser(t("BLT:BLT4"))}</p>
            </div>
          </div>
        </div>

        <div className="container features">
          <div className="row">
            <div className="col-lg-12">
              <img src={bltimg2} className="img-fluid" />
            </div>
          </div>
        </div>

        <div className="bg-lightblue">
          <div className="container container-wrapper">
            <h2 className="text-center mb-4">{t("BLT:BLT5")}</h2>
            <div className="row">
              <div className="col-lg-4 mb-4">
                <div className="card h-100 mb-4">
                  <div className="card-body text-left">
                    <h4>{t("BLT:BLT6")}</h4>
                    <p>{t("BLT:BLT7")}</p>
                  </div>
                </div>
              </div>
              <div className="col-lg-4 mb-4">
                <div className="card h-100 mb-4">
                  <div className="card-body text-left">
                    <h4>{t("BLT:BLT8")}</h4>
                    <p>{t("BLT:BLT9")}</p>
                  </div>
                </div>
              </div>
              <div className="col-lg-4 mb-4">
                <div className="card h-100 mb-4">
                  <div className="card-body text-left">
                    <h4>{t("BLT:BLT10")}</h4>
                    <p>{t("BLT:BLT11")}</p>
                  </div>
                </div>
              </div>
            </div>
            <div className="text-center mt-4">
              <a
                href="https://app.nivesh.com/home/index"
                target="_blank"
                className="btn btn-primary text-center"
              >
                {t('CTAFooter:InvestNow1')}
              </a>
            </div>
          </div>
        </div>
      </main>
    );
  }
}

export default withTranslation()(BLT)
